<template>
  <div class="rank-container">
    <ex-header></ex-header>
    <main class="rank-main el-container">
      <el-table :data="ranks" border>
        <el-table-column prop="uid" label="用户ID"></el-table-column>
        <el-table-column prop="title" label="答题"></el-table-column>
        <el-table-column prop="rank" label="排名"></el-table-column>
        <el-table-column prop="score" label="分数"></el-table-column>
        <el-table-column prop="use_time" label="用时"></el-table-column>
      </el-table>
    </main>
  </div>
</template>

<script>
import Header from "../../components/front/Header.vue";
import { mapActions } from "vuex";
export default {
  name: "rank",
  components: {
    "ex-header": Header
  },
  data() {
    return {
      ranks: []
    };
  },
  methods: {
    ...mapActions("rank", ["getRank"])
  },
  mounted() {
    this.getRank()
      .then(res => {
        this.$set(this, "ranks", res.data.data);
      })
      .catch(err => {
        this.$notify.error({
          title: "获取失败",
          message: err.data.error
        });
      });
  }
};
</script>

<style lang="scss" scoped>
.el-table {
  margin-top: 1rem;
}
</style>
